import React from 'react';
import { Grids } from 'components/GridButtons';
import { FileItems } from 'components/FileItems';
import { Select, MenuItem, FormControl, InputLabel, Typography, Button } from '@mui/material';
import { useState } from 'react';
import AddRoundedIcon from '@mui/icons-material/AddRounded';
import InventoryIcon from '@mui/icons-material/Inventory2TwoTone';
import { Link } from 'react-router-dom';
// import { useEffect } from 'react';

export function ShopListView(props) {
  const [filterItems, setFilterItems] = useState(-1);
  const [showingProducts, setShowingProducts] = useState(
    props.products === null ? [] : props.products
  );

  const onSelect = ($e) => {
    const targetType = $e.target.value;
    setFilterItems(targetType);
    if (targetType === -1) {
      setShowingProducts(props.products);
      return;
    }

    const filtered = props.products.filter((v) => v.Type === targetType);
    setShowingProducts(filtered);
    console.log(showingProducts);
  };

  return (
    <div className="row">
      <div className="col-12">
        <div className="row">
          <div className="col-sm-6">
            <div className="row my-3">
              <div className="col-6 ">
                <Typography variant="h4">محصولات</Typography>
              </div>
              <div className="col-6 py-1">
                <Link to={'../create'} style={{ textDecoration: 'none' }}>
                  <Button variant="contained">
                    <AddRoundedIcon
                      className="rounded-circle shadow ms-3"
                      sx={{ bgcolor: 'rgba(255,255,255,0.2)' }}
                    />
                    اضافه کردن
                  </Button>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-sm-6">
            <FormControl fullWidth>
              <InputLabel id="filter-id">نمایش</InputLabel>
              <Select
                value={filterItems}
                label="نمایش"
                labelId="filter-id"
                className="w-100"
                onChange={onSelect}>
                <MenuItem value={-1}>همه</MenuItem>
                <MenuItem value={0}>سکه ها</MenuItem>
                <MenuItem value={1}>بوستر</MenuItem>
                <MenuItem value={2}>وی آی پی</MenuItem>
                <MenuItem value={3}>کاراکتر</MenuItem>
                <MenuItem value={4}>آواتار</MenuItem>
                <MenuItem value={5}>محیط</MenuItem>
              </Select>
            </FormControl>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            {showingProducts.length === 0 ? (
              <div className="row">
                <div className="col-12 p-5">
                  <Typography>این نوع از دسته بندی وجود ندارد</Typography>
                </div>
              </div>
            ) : (
              <div className="row">
                <div className="col-12 p-3">
                  <Grids>
                    {showingProducts.map((item, index) => (
                      <FileItems
                        Icon={InventoryIcon}
                        Id={item._id}
                        OnSelect={props.onUpdate}
                        key={`shop-${index}-${item._id}`}>
                        {item.Name}
                      </FileItems>
                    ))}
                  </Grids>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default ShopListView;
